import React from "react";
import ReactDOM from "react-dom";
import CommentDetail from "./CommentDetail";
import ApprovalCard from "./ApprovalCard";

import TextField from "@material-ui/core/TextField";
import { IntlProvider, FormattedMessage } from "react-intl";
import Button from "@material-ui/core/Button";
class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      username: "",
      password: ""
    };
  }
  render() {
    return (
      // <div className="ui container comments">
      //    <ApprovalCard>
      //   <div>
      //     <h4>Warning!</h4>
      //     Are you sure you want to do this?
      //   </div>
      //   </ApprovalCard>
      //   <ApprovalCard>
      //   <CommentDetail author="sam" timeago="Today at 4pm" comment="Great blog post!"/>
      //   </ApprovalCard>
      //   <ApprovalCard>
      //   <CommentDetail author="jack" timeago="Today at 5pm" comment="Great blog post!"/>
      //   </ApprovalCard>
      //   <ApprovalCard>
      //   <CommentDetail author="rahul" timeago="Today at 6pm" comment="Great blog post!"/>
      //   </ApprovalCard>
      //</div>
      <IntlProvider locale="en">
        <div>
          <form>
            <TextField
              id="username"
              type="email"
              label={<FormattedMessage id="Username" />}
              className={"mui-input"}
              value={this.state.username}
              margin="normal"
              fullWidth
              onChange={e => this.setState({ username: e.target.value })}
              autoComplete="off"
              helperText={<FormattedMessage id="showError" />}
            />

            <TextField
              id="password"
              type="password"
              label={<FormattedMessage id="Password" />}
              className={"mui-input"}
              margin="normal"
              fullWidth
              value={this.state.password}
              onChange={e => this.setState({ password: e.target.value })}
              helperText={<FormattedMessage id="showError" />}
              autoComplete="off"
            />

            <Button variant="contained" color="primary">
              submit
            </Button>
          </form>
        </div>
      </IntlProvider>
    );
  }
}

ReactDOM.render(<App />, document.querySelector("#root"));
